<template>
    <div>
        <form onsubmit="return false">
            <input
                    type="file"
                    style="display: none;"
                    ref="fileInput"
                    accept="image/*"
                    @change="onMediaPicked"
                    multiple>
        </form>
        <ul style="list-style: none;">
            <li>The trip <b>owner</b> can upload up to <b>10</b> images</li>
            <li>Each <b>rider</b> can upload up to <b>5</b> images</li>
            <li>Uploading new images will <b>delete</b> all previously uploaded images</li>
        </ul>

        <v-btn
                raised
                class="primary"
                round
                v-if="!uploading"
                @click="onPickMedia"
        >
            Upload
        </v-btn>
        <p v-if="uploading">Uploading... <v-progress-circular indeterminate size="30" color="white"></v-progress-circular></p>

        <div v-if="myMedia">
            <v-divider class="mt-3"></v-divider>
            <h3 class="title mt-3">Your uploaded media</h3>
            <media-wall class="mt-3" v-if="myMedia" :media="[myMedia]"></media-wall>
        </div>

    </div>
</template>

<script>
    import MediaWall from './MediaWall'

    export default {
        props: ['experience'],
        components: {
            MediaWall
        },
        data () {
            return {
                imageURLs: [],
                images: [],
                uploading: false,
            }
        },
        methods: {
            onPickMedia () {
                this.$refs.fileInput.click()
            },
            onMediaPicked (event) {
                const files = event.target.files

                if (files.length > this.maxFileUpload) {
                    this.clearUploadData()
                    this.$store.dispatch('errorMessage', 'You can upload up to ' + this.maxFileUpload + ' images.')
                    return
                }

                this.$store.commit('setLoading', true)

                for (let i = 0;i < files.length; i++) {
                    let filename = files[i].name

                    // check for a file extension
                    if (filename.lastIndexOf('.') <= 0) {
                        this.$store.dispatch('errorMessage', 'A file without extension was found.')
                        this.clearUploadData()
                        return
                    }

                    this.uploading = true
                    const fileReader = new FileReader()
                    fileReader.addEventListener('load', () => {
                        this.imageURLs.push(fileReader.result)
                    })
                    fileReader.readAsDataURL(files[i])
                    this.images.push(files[i])
                }

                let payload = {
                    id: this.$route.params.id,
                    images: this.images,
                    previousMediaURLs: []
                }


                // find previous media uploads by this user
                if (this.experience.mediaUploads) {
                    let mediaUpload = this.myMedia

                    // if the user has uploaded media to this experience before
                    if (mediaUpload) {
                        payload.previousMediaURLs = mediaUpload.mediaURLs
                    }
                }

                this.$store.dispatch('uploadMedia', payload).then(mediaUploads => {
                    this.experience.mediaUploads = mediaUploads
                    this.$store.dispatch('successMessage', 'Media Uploaded!')
                    this.clearUploadData()
                    this.$store.commit('setLoading', false)
                    this.uploading = false
                }, error => {
                    this.$store.dispatch('errorMessage', error.message)
                    this.$store.commit('setLoading', false)
                    this.uploading = false
                })
            },
            clearUploadData () {
                this.imageURLs = []
                this.images = []
            },
        },
        computed: {
            user () {
                return this.$store.getters.user
            },
            myMedia () {
                if (this.experience.mediaUploads) {
                    return this.experience.mediaUploads.filter(mediaUpload => {
                        return mediaUpload.user.uid === this.user.uid
                    })[0]
                }
                return null
            },
            maxFileUpload () {
                return this.user.uid === this.experience.owner.uid ? 10 : 5
            }
        },
    }
</script>


