<template>
    <v-container grid-list-md>
        <v-layout row wrap>
            <v-flex v-for="(item, index) in media"
                    :key="'media'+index"
                    class="mb-5"
                    xs12 md6>
                <v-card class="light-card">
                    <v-card-title>
                        <router-link style="text-decoration: none; cursor: pointer; width: 100%;" class="title white--text"
                                     :to="{name: 'Profile', params: { uid: item.user.uid, profile: item.user }}">
                            {{item.user.name}}
                        </router-link>
                        <small><i>{{item.postedAt | datetime}}</i></small>
                    </v-card-title>
                    <v-carousel lazy :cycle="false">
                        <v-carousel-item
                                v-for="(url,i) in item.mediaURLs"
                                :src="url"
                                :key="'photo'+index+i"
                                transition="fade"
                                reverse-transition="fade">
                        </v-carousel-item>
                    </v-carousel>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    export default {
        props: ['media'],
    }
</script>


